// import { Link } from "gatsby"
// import PropTypes from "prop-types"
import React, { useEffect } from "react"

// import {Container, Underline} from '../../styles/globalStyles';
import { PortfolioItemFlex, PortfolioItemTitle, PortfolioItemDescription, PortfolioItemText, PortfolioImage } from '../../styles/portfolioStyles'
// import {text} from '../../constants'


//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

import {parent, child} from '../../animations/animations'

const PortfolioItem = (props) => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <>
        <PortfolioItemFlex reverse={props.reverse} ref={contentRef} variants={parent} initial='hidden' animate={animation}>

            {/* <PortfolioStrip />  */}
            <PortfolioImage variants={child}>
                
                <img src={props.image} alt=""></img>
            </PortfolioImage>   
            <PortfolioItemText variants={child}>
                
                {/* <Container> */}
                {/* <Number>/{props.id}.</Number> */}
                <PortfolioItemTitle>{props.title}</PortfolioItemTitle>
                <PortfolioItemDescription>{props.description}</PortfolioItemDescription>
                
            </PortfolioItemText>
           
        </PortfolioItemFlex>
        </>
    )
}

export default PortfolioItem