import styled, { css } from 'styled-components';
import {motion} from 'framer-motion'
import {theme} from '../globalVariables'

export const Number = styled(motion.div)`
    position: absolute;
    top: -100px; 
    font-size: 5rem;
    opacity: 10%;
    font-family: 'Playfair Display';
`

export const PortfolioItemText = styled(motion.div)`
    position: relative;
    /* width: 700px; */
    grid-area: description;
    /* text-align: center; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: ${theme.defaultBlack};
`

export const PortfolioImage = styled(motion.div)`
    position: relative;
    grid-area: image;
    display: flex;
    justify-content: center;
    img {
        height: auto;
        width: 200px;
        object-fit: cover;  
        
        @media screen and (min-width: 360px) {
            width: 200px;
        }  
    }    
`

export const PortfolioButton = styled(motion.div)`
    
    button {
        width: 100px;
        
        color: black;
        background: transparent;
        border: 1px solid rgba(0, 0, 0, 0.5);
        padding: 10px 10px;
        font-family: 'Poppins', sans-serif;
        /* border-radius: 10px; */
        transition: 0.2s ease-in-out;

        @media screen and (min-width: 300px) {
            width: 150px;
        }

        &:hover {
            cursor: pointer;
            
            color: white;
            background: ${theme.secondaryColor};
            border: 1px solid ${theme.secondaryColor};
        }
    }
`

export const SeeDetails = styled(motion.div)`
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 50px 0 20px;
    a {
        color: black;

        &:hover {
            color: ${theme.secondaryColor};
        }
    }
`

export const DevelopedIn = styled(motion.div)`
    text-transform: lowercase;
    font-size: 0.7rem;
    font-family: 'Poppins';
    color: ${theme.secondaryColor};
    margin-top: 30px;
`

export const PortfolioItemFlex = styled(motion.div)`
    display: flex;
    flex-direction: column;
    
    justify-content: center;
    align-items: center;
    /* flex-direction: column; */
    row-gap: 50px;
    column-gap: 50px;

    /* @media screen and (min-width: 480px) {
        grid-template-areas: 'image' 'description';
        grid-template-rows: 300px;
        grid-template-columns: 300px auto;
    } */

    

    @media screen and (min-width: 580px) {
        
    }
    @media screen and (min-width: 768px) {
        display: grid;
        /* grid-template-areas: 'image' 'description';
        grid-template-rows: 1fr;
        grid-template-columns: 1fr auto; */

        grid-template-areas: 'image description';
        grid-template-rows: auto;
        grid-template-columns: 300px 300px;
        justify-content: space-around;

        ${props => 
        props.reverse && 
        css`
            grid-template-areas: 'description image';
            flex-direction: row-reverse;
        `}    
    }

    @media screen and (min-width: 1000px) {
        
        grid-template-columns: 300px 500px;
    }

    @media screen and (min-width: 1280px) {
        grid-template-columns: 400px 500px;
    }

    @media screen and (min-width: 1440px) {
        
        
        
    }
    @media screen and (min-width: 1920px) {
        
    }
`

export const PortfolioItemDescription = styled(motion.div)`
    margin-top: 10px;
    
    /* @media screen and (min-width: 768px) {
        width: 400px;
    }  */
`

export const PortfolioItemTitle = styled(motion.div)`
    font-size: 1.7rem;
    font-weight: 600;

    &::before {
        content: '';
        display: block;
        width: 150px;
        height: 5px;
        background: ${theme.red};
        margin-bottom: 10px;
    }

    @media screen and (min-width: 760px) {
        
    }
`

export const ItemsFlex = styled(motion.div)`
    display: flex;
    flex-direction: column;
    row-gap: 100px;
`

export const SocialFlex = styled(motion.div)`
    display: flex;
    justify-content: center;
    column-gap: 100px;
    margin-top: 20px;
    width: 100%; 

    img {
        height: 35px;
    }
`