import styled from 'styled-components';
import {motion} from 'framer-motion'
import {theme} from '../globalVariables'
export const Title = styled(motion.div)`
    
    

      
`

export const Image = styled(motion.div)`
    
    --height: 70vh; 
    
    height: var(--height);
    width: 100%;

    &::before {
        content: '';
        position: absolute;
        height: var(--height);
        width: 100%;
        background: linear-gradient(to bottom, rgba(0,0,0, 0) 0%, rgba(0,0,0, 1) 100%);
    }

    img {
        height: var(--height);
        width: 100%;
        object-fit: cover;
    }
`

export const Video = styled(motion.div)`
    
    
    height: 80vh;

    &::before {
        content: '';
        position: absolute;
        height: 80vh;
        width: 100%;
        background: linear-gradient(to bottom, rgba(0,0,0, 0) 0%, rgba(0,0,0, 1) 100%);
    }

    video {
        object-position: center;
        object-fit: cover;
    }
`

export const BriefContainer = styled(motion.div)`
    color: ${theme.defaultWhite};
`

export const BriefBackground = styled(motion.div)`
    background: black;
    
`



