import React, { useEffect } from 'react'

import { IntroductionContainer } from '../../styles/introductionStyles'
import { Container, DefaultSizes, VerticalPadding, Button } from '../../styles/globalStyles'

import { motion } from 'framer-motion'

//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

import {parent, child} from '../../animations/animations'
import { OutboundLink } from 'gatsby-plugin-google-analytics'

const Introduction = (props) => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])
    
    
    
    return(
        <Container>
            <IntroductionContainer ref={contentRef} variants={parent} initial='hidden' animate={animation}>
                <VerticalPadding>
                    <DefaultSizes black>
                        <motion.h1 variants={child}>{props.title}</motion.h1>
                        <motion.p variants={child}>{props.description}</motion.p>
                        {props.button === "true" ? 
                            <div style={{marginTop: '30px'}}>
                                <OutboundLink href="https://behance.net/rodrigo-rocha"><Button variants={child}><button>Clique aqui</button></Button></OutboundLink>
                            </div> : null}
                            
                            
                        
                    </DefaultSizes>
                </VerticalPadding>
            </IntroductionContainer>
            
        </Container>
        
    )
}

export default Introduction;
